
import {
    CDrawer,
    CDrawerBody,
    CDrawerFooter,
    CDrawerHeader,
    CDrawerOverlay,
    CDrawerContent,
    CDrawerCloseButton,
    CButton,
    CIconButton,
    CSelect,
} from '@chakra-ui/vue';
import Vue from 'vue'

export default Vue.extend({
    name: 'roomSettings',
    components: {
        CButton,
        CDrawer,
        CIconButton,
        CDrawerBody,
        CDrawerFooter,
        CDrawerHeader,
        CDrawerOverlay,
        CDrawerContent,
        CSelect,
        CDrawerCloseButton,
    },
    data() {
        return {
            isOpen: false
        }
    },
    methods: {
        close() {
            this.isOpen = false
        },
        changeDeck(newDeckIndex) {
            this.$socket.emit('changeDeck', newDeckIndex)
        }
    },
    computed: {
        availableDecks() {
            return this.$store.getters.availableDecks
        },
        selectedDeck() {
            return this.$store.getters.room?.selectedDeck ?? 0
        }
    },
    // methods: { }
})
